*:not(div.left-nav-container){
  /* border: 1px solid black; */
}

.get-help{
  position: absolute;
  top: 1%;
  right: 2%;
  font-family: sans-serif;
  font-size: 0.9em;
}

div.form-container{
  display: inline-block;
  /* background: red; */
  width: 60%;
  padding: 0px 2em;
  max-width: 600px;
  margin: 0 auto;
  height: 60vh;
  align-self: center;
}

.form-container > h3{
  font-size: 1.3em;
  font-family: sans-serif;
  font-weight: normal;
}

.form-container > p{
  color: gray;
  font-size: 0.9em;
}

.text-field > div > input[type="text"],.text-field > div > input[type="password"]{
  height: 0.1em !important;
  /* background: green; */
}

div.sign-in-container{
  display: flex;
  gap: 0px;
}

.sign-in-button{
  margin-top: 2.5em;
  padding: 0.6em 1.1em;
  font-size: 1em;
  background-color: #341E9D;
  border: none;
  color: white;
  border-radius: 3px;
  font-family: sans-serif;
  float: right;
  /* background: green !important; */
}

div.separator{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  width: 100%;
  margin-top: 8em;
  margin-bottom: 3em;
}

.separator > .separator-item:nth-of-type(2){
  width: 100%;
  text-align: center;
}